<template>
  <div class="comments">
    <div class="header">
      <span @click="back" class="back">
        <img src="../assets/img/back.png" class="releaseIcon">
      </span>
      <span @click="share" class="share">
        分享
      </span>
    </div>
    <div class="container containerInfor">
      <div class="content">
        <div class="row">
          <div class="rowHead">
            <span class="publish">
              <div class="headPic">
                <img :src="blog.fLogo" class="headImg">
              </div>
              <div class="publishInfor">
                <span class="publisher">{{ blog.fNickname }}</span>
                <br>
                <span class="publishTime">{{ blog.fCreatedTime }}</span>
              </div>
            </span>
            <button class="concern" @click="toggleConcern" v-show="blog.fIntType === '0'">
              <img src="../assets/img/jia.png">
              <span>关注</span>
            </button>
            <button class="concerned" @click="toggleConcern" v-show="blog.fIntType !== '0'">
              <span>已关注</span>
            </button>
          </div>
          <div class="blog">{{ blog.fMontNote }}</div>
          <div :class="blog.medias.length === 4 || blog.medias.length === 1 ? picArea2 : picArea1" v-if="blog.fCatType === '0'">
            <span :class="blog.medias.length === 1 ? img1 : img3" v-for="(media, key) in blog.medias" :key="key">
              <img :src="media.media">
            </span>
          </div>
          <div class="videoArea" v-if="blog.fCatType === '1'">
            <video class="picArea2" controls :poster="blog.fMedia1">
              <source :src="blog.fMedia2">
            </video>
          </div>
          <div class="location" v-if="blog.address === 'undefinded' || blog.address === ''">
            <span class="locationIcon">
              <img src="../assets/img/location.png">
            </span>
            <span class="address">{{ blog.address }}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="comments">
      <div class="container">
        <div class="commentSection">
            <div class="commentHead">
              <img src="../assets/img/edit.png">
              <span class="commentTit">评论</span>
              <span class="commentsNum">{{ comments.length }}&ensp;条发言</span>
              <button class="commentBtn" id="commentBtn" @click="comment">写留言</button>
            </div>
            <div class="notice" v-if="comments.length === 0">还没有人评论，我来写第一条评论！</div>
            <div class="commentInfos" v-if="comments.length > 0">
              <div class="commentInfor" v-for="(comment, index) in comments" :key="index">
                <div class="commentator">
                  <span class="commentatorHeadpic">
                    <img :src="comment.fUserPhoto">
                  </span>
                  <span class="commentatorInfor">
                    <span class="commentatorName">{{ comment.fNickname }}</span>
                    <span class="commentTime">{{ comment.fComTime }}</span>
                  </span>
                </div>
                <div class="commentMsg">
                  <span class="messages">{{ comment.fComNote }}</span>
                  <span class="commentImg" @click="replyComment(index)">
                    <img src="../assets/img/comment.png">
                  </span>
                </div>
                <div class="replySection">
                  <div class="replys">
                    <div class="reply" v-for="(reply, replyIndex) in comment.comSonNote" :key="replyIndex">
                      <span class="textOrange">{{ reply.fNickname }}:</span>
                      <span class="replyMessage">{{ reply.fComNote }}</span>
                    </div>
                    <div class="allReplys" v-show="comment.comSonNote.length > 3" @click="showAll = !showAll">
                    <span>显示更多</span>
                    <img :src="showAll ? require('../assets/img/up.png') : require('../assets/img/down.png')">
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="speak" v-show="isShow">
      <input type="text" placeholder="您最多可输入50字!" autofocus="autofocus" id="commentWords" maxlength="50" v-model="commentWord">
      <button type="button" @click="speak">发言</button>
    </div>
    <div class="mask" v-show="hasMask" @click="comment"></div>
  </div>
</template>
<script>
import { getUserInfo, getCommentList, comment, getDynamicListInfo } from '../api/getdata'
import store from '../vuex/store'
import { Toast } from 'mint-ui'
export default {
  name: 'comment',
  data () {
    return {
      myInfo: '',
      commentWord: '',
      blog: {},
      hasMask: false,
      showAll: false,
      isShow: false,
      img1: 'img1',
      img3: 'img3',
      picArea1: 'picArea1',
      picArea2: 'picArea2',
      comments: []
    }
  },
  mounted () {
    // console.log(store.state.commentId)
    this.getInfo()
    this.getBlogInfo()
    this.getCommentInfo()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    share () {
      console.log('share')
    },
    toggleConcern () {
      console.log('关注切换')
    },
    getInfo () {
      let data = getUserInfo({fUserId: localStorage.getItem('userId')})
      data.then(res => {
        console.log(res)
        this.myInfo = res.data
      })
    },
    getBlogInfo () {
      let data = getDynamicListInfo({fMontId: store.state.commentId})
      data.then(res => {
        // console.log(store.state.commentId)
        // console.log(res.data)
        this.blog = res.data
      })
    },
    getCommentInfo () {
      let data = getCommentList({fMontId: store.state.commentId, pageIndex: 1, pageSize: 10})
      data.then(res => {
        console.log(res.data.result)
        this.comments = res.data.result
      })
    },
    speak () {
      this.hasMask = false
      var val = this.commentWord.replace(/(^\s*)|(\s*$)/g, '')
      if (store.state.commentType === 'childComment') {
        if (val === '') {
          Toast({
            message: '输入内容不能为空',
            duration: true
          })
          this.isShow = false
          this.commentWord = ''
        } else {
          this.isShow = false
          this.showAll = true
          let data = comment({fMontId: store.state.commentId, fComNote: val, fRepiyUserId: store.state.replyId, fParentId: store.state.parentId})
          data.then(res => {
            console.log(res)
            this.getCommentInfo()
          })
          this.commentWord = ''
        }
      } else {
        if (val === '') {
          Toast({
            message: '输入内容不能为空',
            duration: true
          })
          this.isShow = false
          this.commentWord = ''
        } else {
          this.isShow = false
          this.showAll = true
          let data = comment({fMontId: store.state.commentId, fComNote: val})
          data.then(res => {
            console.log(res)
            var resComments = res.data
            for (let i = 0; i < resComments.length; i++) {
              this.$set(resComments[i], 'comSonNote', [])
              this.comments.unshift(resComments[i])
            }
          })
          this.commentWord = ''
        }
      }
    },
    comment () {
      store.commit('getCommentType', 'parentComment')
      this.isShow = !this.isShow
      this.hasMask = !this.hasMask
    },
    replyComment (index) {
      store.commit('getCommentType', 'childComment')
      store.commit('getReplyer', this.comments[index].fUserId)
      store.commit('getParentId', this.comments[index].fMontComId)
      this.isShow = !this.isShow
      this.hasMask = !this.hasMask
    }
  }
}

</script>

<style scoped>
  * {
    font-size: 0.5rem;
  }

  .comments {
    width: 100%;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    background: #fff;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .share {
    font-size: 0.45rem;
  }

  .releaseIcon {
    width: 100%;
  }

  .container {
    width: calc(100% - 0.625rem);
    margin: 0 auto;
  }

  .container.containerInfor {
    margin-top: 1.5rem;
  }

  .picArea1,
  .picArea2 {
    margin-top: 0.2rem;
  }

  .picArea2 {
    width: 66.667%;
  }

  .picArea2::after,
  .picArea1::after {
    content: "";
    display: block;
    clear: both;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .img3 {
    display: block;
    float: left;
    width: 2.96rem;
    height: 1.947rem;
    margin: 0.08rem;
  }

  .img1 {
    width: 6rem;
  }

  .picArea {
    margin: 0.2rem 0;
  }

  .picArea::after {
    content: "";
    display: block;
    clear: both;
  }

  .rowHead {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .publish {
    display: flex;
    align-items: center;
  }

  .publishInfor {
    text-align: left;
    margin-left: 0.5rem;
  }

  .headPic {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    overflow: hidden;
  }

  .publisher {
    font-size: 0.45rem;
  }

  .publishTime {
    color: #666666;
    font-size: 0.32rem;
    line-height: 0.65rem;
  }

 .concern {
    width: 1.7333rem;
    height: 0.7733rem;
    border: 1px solid #333333;
    border-radius: 0.38667rem;
    background: transparent;
    color: #333333;
    outline: none;
    font-size: 0.4rem;
    font-weight: 500;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .concerned {
    width: 1.7333rem;
    height: 0.7733rem;
    border: 1px solid #999;
    border-radius: 0.38667rem;
    background: transparent;
    color: #999;
    outline: none;
    font-size: 0.4rem;
    font-weight: 500;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .concern img {
    width: 0.36rem;
    height: 0.36rem;
    vertical-align: middle;
  }

  .concern span,
  .concerned span {
    vertical-align: middle;
    font-size: 0.4rem;
  }

  .blog {
    margin-top: 0.5rem;
    text-align: start;
    font-size: 0.4rem;
  }

  .picArea {
    margin-top: 0.2rem;
  }

  .headImg {
    width: 100%;
    height: 100%;
  }

  .location {
    display: flex;
    align-items: baseline;
    line-height: 1rem;
  }

  .locationIcon {
    height: 0.4rem;
    width: 0.275rem;
    display: inline-block;
  }

  .locationIcon img {
    width: 100%;
    height: 100%;
  }

  .address {
    font-size: 0.35rem;
    color: #666666;
    margin-left: 0.1rem;
  }

  .commentSection {
    margin-top: 0.5rem;
  }

  .commentHead {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .commentHead img {
    width: 0.5rem;
    height: 0.5rem;
    margin-bottom: 0.0875rem;
  }

  .commentTit {
    font-size: 0.4rem;
    margin-left: 0.2rem;
  }

  .commentsNum {
    margin-left: 0.5rem;
    font-size: 0.3rem;
    color: #666666;
  }

  .commentator {
    display: flex;
    align-items: center;
    margin-top: 0.5rem;
  }

  .commentatorHeadpic {
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    border-radius: 0.6rem;
    overflow: hidden;
  }

  .commentatorHeadpic img {
    width: 100%;
    height: 100%;
  }

  .commentatorInfor {
    margin-left: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: calc(100% - 1.4rem)
  }

  .img2 {
    width: 50%;
    float: left;
  }

  .commentatorName {
    font-size: 0.45rem;
  }

  .commentTime {
    font-size: 0.4rem;
    color: #666666;
  }

  .commentMsg {
    text-align: left;
    margin-top: 0.2rem;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
 }

  .messages {
    display: inline-block;
    width: 100%;
    text-align: start;
    font-size: 0.45rem;
    word-wrap: break-word;
  }

  .pinglun span,
  .dianzan span {
    font-size: 0.4rem;
  }

  .pinglun,
  .dianzan {
    margin: 0 0.3rem;
  }

  .commentArea {
    text-align: right;
    margin-top: 0.2rem;
    padding-bottom: 0.2rem;
    border-bottom: 1px solid #f5f5f5;
  }

  .commentArea img {
    width: 0.4rem;
    height: 0.4rem;
  }

  .speak {
    width: 100%;
    height: 1.2rem;
    position: fixed;
    bottom: 0;
    padding: 0.2rem;
    border-top: 2px solid #f5f5f5;
    display: flex;
    justify-content: space-between;
    background: #fff;
    font-size: 0.45rem;
  }

  .speak input {
    width: calc(100% - 3.7rem);
    padding: 0.4rem;
    border: 1px solid #f5f5f5;
    border-radius: 0.2rem;
    outline: none;
  }

  .speak button {
    width: 2rem;
    margin-right: 0.5rem;
    border: 1px solid #f5f5f5;
    border-radius: 0.2rem;
    background: transparent;
    outline: none;
  }

  .commentBtn {
    color: #ff8309;
    outline: none;
    border: none;
    margin-left: 4.1rem;
    background: transparent;
    font-size: 0.426667rem;
    padding: 0;
  }

  .replySection {
    background: #f5f5f5;
    width: 100%;
    border-radius: 0.15rem;
  }

  .comments {
    margin-top: 0.5rem;
    border-top: 0.10667rem solid #f5f5f5;
  }

  .replys {
    margin-top: 0.293333rem;
  }

  .reply {
    padding: 0 0.1rem 0 0.3rem;
    text-align: start;
    display: flex;
    align-items: baseline;
  }

  .reply span {
    font-size: 0.373333rem;
    color: #333333;
  }

  .textOrange {
    color: #fd8309!important;
    flex:0 0 auto;
    display: flex;
    align-items: flex-start;
  }

  .allReplys {
    padding: 0.2rem;
    text-align: start;
  }

  .allReplys span {
    font-size: 0.373333rem;
    color: #666666;
  }

  .allReplys {
    display: flex;
    align-items: center;
  }

  .allReplys img {
    width: 0.3rem;
    height: 0.15rem;
    margin-left: 0.1rem;
  }

  .mask {
    z-index: 99;
    position: absolute;
    bottom: 1.6rem;
    width: 100%;
    height: 2000%;
    background: #ffffff;
    opacity: 0;
  }

  .commentImg {
    display: flex;
    width: 0.45rem;
  }

  .commentInfor {
    padding-bottom: 0.2rem;
    border-bottom: 2px solid #f5f5f5;
  }

  .notice {
    margin-top: 0.2rem;
    font-size: 0.37333rem;
    color: #666666;
  }

  .videoArea {
    text-align: start;
  }

  .replyMessage {
    margin-left: 0.2rem;
    word-break: break-all;
    padding-right: 0.2rem;
    display: flex;
    align-items: flex-start;
  }
</style>



